import {format} from "../../../util/util.js"
import {driveDay,driveMonth,obdInstall} from "../../../api/user.js"
export default {
	data() {
		return {
			id:'',//车id
			index: 0, // 0 日数据  1 月数据
			monthClickCount:0,
			startVal:'2019-03-06',
			maxTime:format(new Date().getTime(),'day'),
			dateD:'', // 日期数据
			dateM:'', // 月份数据
			info:{} ,//数据
			
		}
	},
	onLoad(options) {
		uni.hideShareMenu() 
		
		this.id = options.carId
		this.dateD = format(new Date().getTime(),'day')
		this.dateM = format(new Date().getTime(),'month')
		this.getObjInstallTime() 
	},
	methods: {
		// 获取obd安装时间
		getObjInstallTime(){
			obdInstall({
				data:{
					car_id:this.id
				}
			}).then(res=>{
				if(res.data.code == 0) {
					this.startVal = res.data.result.created_date
					this.getDayData()
				}
			})
		},
		// 获取日数据
		getDayData(){
			driveDay({
				data:{
					car_id:this.id,
					time:this.dateD
				}
			}).then(res=>{
				if(res.data.code == 0) {
					this.info = res.data.result.drive_history_day
				}
			})
		},
		getMonthData(){
			driveMonth({
				data:{
					car_id:this.id,
					time:this.dateM
				}
			}).then(res=>{
				if(res.data.code == 0) {
					this.info = res.data.result.drive_history_month
				}
			})
		},
		switchNav(i) {
			if (this.index == i) return
			this.index = i
			if(i == 0) {
				this.getDayData()
			}
			if(i == 1){
				this.getMonthData()
			}
		},
		changePicker(v) {
			
			if(this.index == 0){
				// 日报表
				this.dateD = v.detail.value
				this.getDayData()
			}
			if(this.index == 1){
				// 月报表
				this.dateM = v.detail.value
				this.getMonthData()
			}
		}
	}
}
